.login-container {
  min-height: 61vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--card-bg);
}
.login-box {
   background-color: var(--card-bg);
  padding: 30px 50px;
  width: 350px;
  border-radius: 15px;
  box-shadow: 0 15px 20px rgba(0, 0, 0, 0.12);
}
.login-box h1 {
  text-align: center;
  margin-bottom: 40px;
  margin-top: 15px;
  font-size: 1.4rem;
  color: var(--text-color);
}
.login-box label {
  font-size: 0.9rem;
  color: #555;
}
.login-box input {
  width: 100%;
  padding: 10px;
  margin: 6px 0 14px 0;
  border-radius: 6px;
  border: 1px solid #ccc;
  font-size: 0.9rem;
}

.login-box input:focus {
  outline: none;
  border-color: #1976d2;
}
.login-box button {
  width: 100px;
  padding: 10px;
  margin-left: 125px;
  background-color: #000000;
  color: var(--text-color);
  border: none;
  border-radius: 6px;
  font-size: 0.95rem;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.login-box button:hover {
  background-color: #125aa3;
}
